export function addUpdateQueue(list = [], key) {
    // return list.forEach(fn => fn())
    if (isUpdating) {
        // å¦‚æœå·²è¿›å…¥æ›´æ–°ä¹‹ä¸­ï¼Œåˆ™æ‰€æœ‰å› ä¸ºæ›´æ–°åŠ å…¥é˜Ÿåˆ—ä¹‹ä¸­çš„ä»»åŠ¡éƒ½ä¼šç«‹å³æ‰§è¡Œ
        list.forEach(fn => fn());
        return;
    }
    clearTimeout(timeout);
    queue.push(...list);
    // if (!time) {
    //     time = Date.now()
    // }
    // realtime = Date.now()
    // setTimeoutæ‰§è¡Œæ—¶é—´æ˜æ˜¾æ¯”0msè¦é•¿å¾ˆå¤šï¼Œä½†æ˜¯Performanceå¹¶æ²¡æœ‰è®°å½•å‡½æ•°æ‰§è¡Œ
    // ç»æ’æŸ¥å‘ç°æ˜¯macä¸Šchromeçš„é¡µé¢æ»šåŠ¨é˜»å¡äº†å¼‚æ­¥äº‹ä»¶çš„æ‰§è¡Œ
    timeout = setTimeout(forceUpdate, 0);
}
export default function forceUpdate(afterFn = () => { }) {
    // console.log('setTimeoutç­‰å¾…æ—¶é•¿', Date.now() - realtime)
    isUpdating = true;
    const haveExec = [];
    queue.forEach(fn => {
        // æŸ¥çœ‹æ˜¯å¦æ‰§è¡Œ
        if (!haveExec.includes(fn)) {
            haveExec.push(fn);
            fn();
        }
    });
    isUpdating = false;
    queue = [];
    // console.log('æ‰§è¡Œæ—¶é•¿', Date.now() - time)
    time = 0;
    afterFn();
}
